        </van-button >
        <van-dialog
                v-model="show"
                title="截图展示"
                show-cancel-button
        >
          <img src="../../assets/image/evaluation.png" class="picture">
        </van-dialog>
      </van-col>
      <van-col span="4.5">
        <van-button
                plain
                @click="onClickAlertRules"
