      >
        <div >
          <img src="@/assets/image/photograph.png" class="Photograph"   name="image">
        </div>
      </van-uploader>
        <span v-show="errors.has('上传截图')">{{ errors.first('上传截图') }}</span>
      </div>
    <van-button
            v-on:click="submitApply"
            type="primary"
            size="large"
            style="border-radius:5px;"
    >
      {{button2}}
    </van-button>
    <!--  右下角 + 的跳转  -->
    <div class="routerLink" v-show="showService">
      <van-row>
        <van-icon name="service-o" class="iconService"  @click="customer = true"/>
