<script>
import { Button, Toast, CellGroup, Field, Uploader, Row, Col, Icon, Popup } from 'vant'
import api from '@/api/'
// import VConsole from 'vconsole/dist/vconsole.min.js' // import vconsole
// let vConsole = new VConsole() // 初始化

export default {
  components: {
    [Button.name]: Button,
    [CellGroup.name]: CellGroup,
    [Field.name]: Field,
    [Uploader.name]: Uploader,
    [Row.name]: Row,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Popup.name]: Popup
  },
  data () {
    return ({
      orderNum: null,
      image: null,
      img: [], // 用于显示图片
      show: false,
      customer: false,
      showResult: false,
      show1: false,
      showService: true,
      noshowService: false,
      button: '截图示例',
      button1: '活动规则',
      button2: '确定',
      button3: '我的任务',
      button4: '我的教程',
      button5: '联系客服'
    })
  },
  methods: {
    async validate () {
      const result = await this.$validator.validate()
      if (!result) {
        const errMsg = this.errors.items[0].msg
        throw new Error(errMsg)
      }
    },
    // uploader 上传图片，先将图片上传到本地，然后再点击确认按钮
    // async upload (files) {
    //   console.log(files)
    //   var file = files.file
    //   var fe = new FormData()
    //   for (var i = 0; i < file.length; i++) {
    //     fe.append('up[]', file[i])
    //   }
    //   const { data3 } = await this.$api.apply.show({
    //     screen_shot: fe, data: fe, cache: false, processData: false, contentType: false })
    //   // 成功回调就将图片缩略图显示出来
    //     .then(
    //       res => { this.img.push(res.data.imgpath) }
    //     )
    //   // 失败回调
    //     .catch(err => { alert(err) })
    // },
    async onRead (e) {
      // new FormData来进行提交，回调成功就显示缩略图，进行预览展示
      console.log(e)
      let fd = new FormData()
      fd.append('upfile', JSON.stringify(e))
      fd.append('upfile', e.file)
      console.log(fd.getAll('upfile'))
      console.log(fd.has('upfile')) // 检测 formData 是否存在upfile
      const params = {
        screen_shot: fd.getAll('upfile')
      }
      console.log(params)
      const { data } = await this.$api.apply.show(params)
      // 成功回调就将图片缩略图显示出来
      //   .then(
      //     res => { console.log(res) }
      //     // res => { alert(this.img.push(res)) }
      //     // this.img.push(e.content)
      //   )
      // // 失败回调
      //   .catch(err => { alert(err) })
      if (data) {
        console.log(data.desc)
      } else {
        console.log(data.desc)
      }
    },
    // 删除预览图片
    imgclose (e) {
      this.img.splice(e, 1)
    },
    async submitApply () {
      const params = {
        order_id: this.orderNum,
        screen_shot: this.img[0]
      }
      console.log(params)
      try {
        await this.validate()
        const { data } = await api.apply.commentCreate(params)
        if (data.code !== 200) {
          Toast(data.desc)
        } else {
          Toast(data.desc)
        }
        console.log(params.image)
        console.log(params.order_id)
      } catch (err) {
        // 提示错误信息
        Toast(err.message)
      }
    },
    // 活动规则的弹窗信息
    onClickAlertRules () {
      this.$dialog.alert({
        title: '活动规则',
        message: '1.按照要求完成评价截图上传，订单号上传。 2.人工审核后发放红包。'
      })
    },
    // 点击加号，替换减号列表
    changeMin () {
      this.showService = false
      this.noshowService = true
      console.log('showService：' + this.showService)
      console.log('noshowService：' + this.noshowService)
    },
    // 点击减号，替换加号列表
    changeAdd () {
      this.showService = true
      this.noshowService = false
      console.log('showService：' + this.showService)
      console.log('noshowService：' + this.noshowService)
    }
  }
}
</script>

